@import url('https://fonts.googleapis.com/css?family=Odibee+Sans&display=swap');
@import url('https://fonts.googleapis.com/css?family=Saira+Semi+Condensed&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background: url('pixelart.jpg') no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

:root {
  --dark-blue: #5075968e;
  --violet: #967b909c;
  --whiten: rgba(255, 252, 252, 0.247);
  --header-font-size: 22px;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
  border-radius: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--whiten);
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--dark-blue);
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--violet);
}

body {
  overflow-y: scroll;
}

.header {
  display: flex;
  justify-content: flex-end;
  padding-right: 20px;
  height: 50px;
  background-color: RGB(186, 130, 143);
  width: 100%;
}

.headerItem {
  padding-top: 10px;
  margin: 5px;
  color: #fffcfc;
  font-family: 'Odibee Sans', cursive;
  font-size: var(--header-font-size);
  transition: 0.2s;
}

.headerItem:hover {
  letter-spacing: 2px;
  font-size: 25px;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #507596, 0 0 30px #507596,
    0 0 40px #507596, 0 0 55px #507596, 0 0 75px #507596;
  text-align: center;
}

.myBody {
  width: auto;
}

.hello {
  padding-top: 50px;
  font-family: 'Odibee Sans', cursive;
  font-size: 50px;
  line-height: 60pt;
  text-align: center;
  color: #fffcfc;
}

.contact {
  border-radius: 2px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  padding: 10px;
  color: #fffcfc;
  font-family: 'Odibee Sans', cursive;
  font-size: 20px;
  width: 100%;
  letter-spacing: 1px;
  line-height: 50pt;
  text-align: center;
}

.miniIcon {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: between;
}

a {
  color: white;
  text-decoration: none;
}

.wouldHelp {
  font-family: 'Odibee Sans', cursive;
  font-size: 50px;
  text-align: center;
  color: #fffcfc;
  letter-spacing: 2px;
  padding-top: 40px;
}

.contactContainer {
}

hr {
  width: 90%;
  height: 4px;
  background-color: #fffcfc;
  margin-left: auto;
  margin-right: auto;
}

.contactIcon {
  display: flex;
  justify-content: center;
  flex-direction: row;
}
.icon {
  align-self: center;
  background-color: #967b90;
  padding: 5px;
  margin: 15px;
  width: 40px;
  height: 40px;
}

.aboutContainer {
}

.projectsContainer {
  height: calc(100vh - 50px);
  width: 95vw;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.projectItem {
  background-color: rgba(252, 252, 252, 0.2);
  width: 60vw;
  height: auto;
  padding: 35px 15px 10px 20px;
  border: solid #fffcfc 1px;
  font-family: 'Saira Semi Condensed', sans-serif;
  align-self: center;
  margin: 20px;
}

.projectImage {
  width: 180px;
  height: 180px;
  float: right;
  padding-top: 10px;
}

.projectDesc {
  color: #fffcfc;
  width: 50vw;
  margin-bottom: 10px;
  font-size: 18px;
  margin-left: 10px;
}

.projectName {
  font-size: 25px;
  font-weight: 600;
  color: #fffcfc;
  margin-left: 10px;
}
.projectView {
  padding-top: 10px;
}

.viewDetails {
  padding-top: 10px;
  margin-left: 10px;
}
.blogContainer {
  height: calc(100vh - 50px);
  width: 95vw;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.blogItem {
  background-color: rgba(252, 252, 252, 0.2);
  width: 60vw;
  padding: 35px 15px 10px 20px;
  border: solid #fffcfc 1px;
  font-family: 'Saira Semi Condensed', sans-serif;
  align-self: center;
  margin: 20px;
}

.blogName {
  font-size: 25px;
  font-weight: 600;
  color: #fffcfc;
  margin-left: 10px;
  text-align: center;
  margin-top: 10px;
}

.blogImage {
  width: 70%;
  padding-top: 20px;
  display: block;
  margin-right: auto;
  margin-left: auto;
}

.blogDate {
  color: #fffcfc;
  font-size: 15px;
  text-align: center;
  text-decoration: underline;
}

.blogPost {
  color: #fffcfc;
  overflow-y: hidden;
  height: 300px;
  width: auto;
  font-size: 22px;
  font-weight: 600;
  margin: 30px;
  text-align: justify;
}

.expand {
  height: auto !important;
}

.blogShare {
  display: flex;
  justify-content: flex-start;

  margin: 30px;
}

.blogIcon {
  width: 20px;
  height: 20px;
  margin: 8px;
}

.blogIcon:hover {
  box-shadow: 0 0 5px #fff;
  border-radius: 5px;
}

.blogMore {
  border: solid #fffcfc 2px;
  background-color: var(--whiten);
  width: 100px;
  height: 42px;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  padding: 5px;
  margin: 5px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  color: var(--dark-blue);
}

.techgig {
  font-size: 10px;
}

.meetMe {
  font-family: 'Odibee Sans', cursive;
  font-size: 50px;
  text-align: center;
  color: #fffcfc;
  letter-spacing: 2px;
  padding-top: 15px;
}

.aboutImage {
  transform: rotate(90deg);

  width: 200px;
  display: block;
  margin-top: 35px;
  border: solid #fffcfc 3px;
  margin-left: auto;
  margin-right: auto;
}

.meExplained {
  font-family: 'Saira Semi Condensed', sans-serif;
  font-size: 25px;
  font-weight: 400;
  text-align: center;
  color: #fffcfc;
  letter-spacing: 2px;
  padding-top: 25px;
  text-shadow: 1px 1px #fffcfc;
}

.icon:hover {
  width: 50px;
  height: 50px;
  box-shadow: 0 0 15px #e4a9d7;
  border-radius: 5px;
}

@media only screen and (max-width: 800px) {
  .headerItem {
    font-size: 20px;
  }
  .header {
    justify-content: center;
  }
  .headerItem {
    align-items: center;
  }

  .hello {
    font-size: 45px;
    line-height: 48pt;
  }

  .aboutContainer {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .meetMe {
    font-size: 42px;
    text-align: center;
  }
  .meExplained {
    font-size: 20px;
    text-align: center;
    padding: 10px;
    padding-top: 35px;
  }

  .miniIcon {
    font-size: 17px;
  }

  .projectItem {
    width: 90vw;
    align-self: center;
    margin: 15px;
  }
  .projectImage {
    width: 120px;
    height: 120px;
  }
  .projectDesc {
    font-size: 13px;
    margin-left: 10px;
  }

  .projectName {
    font-size: 18px;
    font-weight: 400;
  }
  .blogItem {
    width: 90vw;
    align-self: center;
  }

  .blogName {
    font-size: 18px;
    font-weight: 400;
  }

  .blogImage {
    width: 180px;
    height: 120px;
  }

  .blogPost {
    font-size: 13px;
  }

  .blogMore {
    width: 75px;
    height: 35px;
    font-size: 18px;
    padding-bottom: 5px;
  }

  .blogIcon {
    width: 17px;
    height: 17px;
  }

  .blogShare {
    justify-content: center;
  }

  .wouldHelp {
    font-size: 60px;
    text-align: center;
  }
}

@media only screen and (max-width: 400px) {
  .headerItem {
    font-size: 18px;
  }
  .header {
    justify-content: center;
  }
  .headerItem {
    align-items: center;
  }

  .hello {
    font-size: 40px;
    line-height: 45pt;
  }

  .miniIcon {
    font-size: 17px;
  }

  .aboutContainer {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .meetMe {
    font-size: 40px;
    text-align: center;
  }
  .meExplained {
    font-size: 17px;
    text-align: center;
    padding: 10px;
    padding-top: 35px;
  }

  .projectItem {
    width: 90vw;
    align-self: center;
    margin: 15px;
  }
  .projectImage {
    width: 120px;
    height: 120px;
  }
  .projectDesc {
    font-size: 13px;
    margin-left: 10px;
  }

  .projectName {
    font-size: 18px;
    font-weight: 400;
  }
  .blogItem {
    width: 90vw;
    align-self: center;
  }

  .blogName {
    font-size: 18px;
    font-weight: 400;
  }

  .blogImage {
    width: 180px;
    height: 120px;
  }

  .blogPost {
    font-size: 13px;
  }

  .blogMore {
    width: 75px;
    height: 35px;
    font-size: 18px;
    padding-bottom: 5px;
  }

  .blogIcon {
    width: 17px;
    height: 17px;
  }

  .blogShare {
    justify-content: center;
  }

  .wouldHelp {
    font-size: 40px;
    text-align: center;
  }
}
